import React from 'react';
import PropTypes from 'prop-types';
import treeChanges from 'tree-changes';
import { Container, Screen } from 'styled-minimal';
import { STATUS } from 'constants/index';
import { getRepos, showAlert } from 'actions';
import Loader from 'components/Loader';
import Pagination from 'components/Pagination';
import Sidebar from 'components/Sidebar';
import { connect } from 'react-redux';
import moment from 'moment';
import '../assets/css/all.min.css';
import '../assets/css/custom.css';

export class Private extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      query: 'INBOX',
      page: 1,
    };
    this.handleChangeLabels = this.handleChangeLabels.bind(this);
    this.onPageChanged = this.onPageChanged.bind(this);
  }

  static propTypes = {
    dispatch: PropTypes.func.isRequired,
    github: PropTypes.object.isRequired,
  };

  componentDidMount() {
    const { query, page } = this.state;
    const { dispatch } = this.props;

    dispatch(getRepos({ query, page }));
  }

  componentDidUpdate(prevProps) {
    const { dispatch, github } = this.props;
    const { changedTo } = treeChanges(prevProps, this.props);

    if (changedTo('github.repos.status', STATUS.ERROR)) {
      dispatch(showAlert(github.repos.message, { variant: 'danger' }));
    }
  }

  handleChangeLabels(event) {
    const { query, page } = this.state;
    const { dispatch } = this.props;
    this.setState({
      query: event,
    });
    dispatch(getRepos({ query, page }));
  }

  onPageChanged(data) {
    const { query, page } = this.state;
    const { dispatch } = this.props;
    const { currentPage } = data;
    this.setState({ page: currentPage });
    dispatch(getRepos({ query, page }));
  }

  render() {
    const { query } = this.state;
    const { github } = this.props;
    const data = github.repos.data[query] || [];
    const labels = github.repos.data.labels || [];
    let output;

    if (github.repos.status === STATUS.SUCCESS) {
      if (data.length) {
        output = github.repos.data[query].map(d => (
          <tr key={d.id}>
            <td>
              <div className="icheck-primary">
                <input type="checkbox" value="" id="check1" />
              </div>
            </td>
            <td className="mailbox-star">
              <a href="#">
                <i className="fas fa-star text-warning" />
              </a>
            </td>
            <td className="mailbox-name">
              <a href="read-mail.html">{d.subject}</a>
            </td>
            <td className="mailbox-subject">{d.snippet}</td>
            <td className="mailbox-attachment" />
            <td className="mailbox-date">
              {moment.unix(d.mail_date).format('MMMM Do YYYY, h:mm:ss a')}
            </td>
          </tr>
        ));
      } else {
        output = <h3>Nothing found</h3>;
      }
    } else {
      output = <Loader block />;
    }
    return (
      <Screen key="Private" data-testid="PrivateWrapper">
        <Container>
          <>
            <div className="wrapper">
              <div className="content-wrapper">
                <section className="content-header">
                  <div className="container-fluid">
                    <div className="row mb-2">
                      <div className="col-sm-6">
                        <h1>Inbox</h1>
                      </div>
                    </div>
                  </div>
                </section>
                <section className="content">
                  <div className="row">
                    <div className="col-md-3">
                      <a href="#" className="btn btn-primary btn-block mb-3">
                        Compose
                      </a>
                      <div className="card">
                        <div className="card-header">
                          <h3 className="card-title">Folders</h3>

                          <div className="card-tools">
                            <button
                              type="button"
                              className="btn btn-tool"
                              data-card-widget="collapse"
                            >
                              <i className="fas fa-minus" />
                            </button>
                          </div>
                        </div>
                        {github.repos.status === STATUS.SUCCESS ? (
                          <Sidebar labels={labels} action={this.handleChangeLabels} />
                        ) : (
                          <Loader block />
                        )}
                      </div>
                    </div>
                    <div className="col-md-9">
                      <div className="card card-primary card-outline">
                        <div className="card-header">
                          <h3 className="card-title">Inbox</h3>
                          <div className="card-tools">
                            <div className="input-group input-group-sm">
                              <input
                                type="text"
                                className="form-control"
                                placeholder="Search Mail"
                              />
                              <div className="input-group-append">
                                <div className="btn btn-primary">
                                  <i className="fas fa-search" />
                                </div>
                              </div>
                            </div>
                          </div>

                          <div className="card-body p-0">
                            <div className="mailbox-controls">
                              <button
                                type="button"
                                className="btn btn-default btn-sm checkbox-toggle"
                              >
                                <i className="far fa-square" />
                              </button>
                              <div className="btn-group">
                                <button type="button" className="btn btn-default btn-sm">
                                  <i className="far fa-trash-alt" />
                                </button>
                                <button type="button" className="btn btn-default btn-sm">
                                  <i className="fas fa-reply" />
                                </button>
                                <button type="button" className="btn btn-default btn-sm">
                                  <i className="fas fa-share" />
                                </button>
                              </div>
                              <button type="button" className="btn btn-default btn-sm">
                                <i className="fas fa-sync-alt" />
                              </button>
                            </div>
                            <div className="table-responsive mailbox-messages">
                              <table className="table table-hover table-striped">
                                <tbody>{output}</tbody>
                              </table>
                              <div>
                                {github.repos.status === STATUS.SUCCESS && (
                                  <Pagination
                                    totalRecords={github.repos.data.numberOfPage}
                                    pageLimit={10}
                                    pageNeighbours={1}
                                    onPageChanged={this.onPageChanged}
                                  />
                                )}
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </section>
              </div>
            </div>
          </>
        </Container>
      </Screen>
    );
  }
}
function mapStateToProps(state) {
  return { github: state.github };
}
export default connect(mapStateToProps)(Private);
